import React, { useState, useEffect } from 'react'
import Blog from './components/Blog'
import blogService from './services/blogs'

const App = () => {
  const [blogs, setBlogs] = useState([])
  const [title, setTitle] = useState()
  const [url, setUrl] = useState()
  const [author, setAuthor] = useState()
  const [username, password] = useState()
  const [errorMessage, seterrorMessage] = useState()
  useEffect(() => {
    blogService.getAll().then(blogs =>
      setBlogs(blogs)
    )
  }, [])


  const handleSubmit = (title, url, author) => {
    const newBlog = {
      title: title,
      author: author,
      url: url
    }

    blogService.postNewBlog(newBlog).then(savedBlog => {
      const newBlogs = blogs.concat(savedBlog)
      setBlogs(newBlogs)
      console.log(newBlogs)
    }
    )
  }
  return (
    <div>
      <h2>blogs</h2>
      {blogs.map(blog =>
        <Blog key={blog.id} blog={blog} />
      )}
      <form onSubmit={() => handleSubmit(title, url, author)}>
        <label for="title">title</label>
        <input value={title} name="title"></input>
        <label for="author">author</label>
        <input value={author} name="author"></input>
        <label for="url">url</label>
        <input value={url} name="url"></input>
        <input type="submit" value="save"></input>
      </form>
    </div >
  )
}

export default App